}
.c-products__pagination .page-numbers.current {
  background: #4D4D4D;
  color: #fff;
  border-color: #4D4D4D;
}
.c-products__pagination .page-numbers.prev, .c-products__pagination .page-numbers.next {
  position: relative;
}
.c-products__pagination .page-numbers.prev::before, .c-products__pagination .page-numbers.next::before {
  content: "";
  width: 0.8rem;
  height: 0.8rem;
  border-top: 1px solid #4D4D4D;
  border-right: 1px solid #4D4D4D;
  position: absolute;
  top: 50%;
  left: 50%;
}
.c-products__pagination .page-numbers.prev:hover::before, .c-products__pagination .page-numbers.next:hover::before {
  opacity: 0.7;
}
.c-products__pagination .page-numbers.prev::before {
  transform: translate(-25%, -50%) rotate(-135deg);
}
.c-products__pagination .page-numbers.next::before {
  transform: translate(-75%, -50%) rotate(45deg);
}
