@use "global"as *;
@use "sass:math";

/*
* wrapper
*/
// デフォルトの横幅
.l-base {
  margin-left: $width-m-sp;
  margin-right: $width-m-sp;

  @include media(tb) {
    width: min(calc(100% - #{$width-m-sp * 2}), #{$width});
    margin-left: auto;
    margin-right: auto;
  }
}

.l-wide {
  margin-left: $width-m-sp;
  margin-right: $width-m-sp;

  @include media(tb) {
    width: min(calc(100% - #{$width-m-sp * 2}), #{$width-wide});
    margin-left: auto;
    margin-right: auto;
  }
}